.App_Header{
    display: flex;
    justify-content: space-between;
    color: #FFFFFF;
    border-bottom: 1px solid #D8D8D81A;
    position: fixed;
    top: 0;
    width: 100%;
    background-color: #2B2C3A;
}
.App_SideBarHeader{
    background-color: #9CA5EC0A;
    width: 280px;
    padding-left: 30px;
    height: 71px;
    display: flex;
    align-items: center;
    box-sizing: border-box;
}
.App_SideBarHeaderYear{
    font-size: 11px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.App_SideBarHeaderBrand{
    font-size: 22px;
    font-weight: bold;
    line-height: 22px;
    height: 22px;
}
.App_Content{
    box-sizing: border-box;
    display: flex;
    width: calc(100vw - 280px);
    position: absolute;
    right: 0;
    top: 72px;
    z-index: -1;
    padding-left: 30px;
}