.AppHeaderRightPart_Icon {
    height: 18px;
    width: 18px;
    background-color: #FF4738;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4px;
    box-shadow: 0 0 12px rgba(255, 71, 56, 0.5);
}
.AppHeaderRightPart {
    display: flex;
    gap: 10px;
    background-color: #2B2C3A;
    color:rgba(255,255,255,0.6);
}
.AppHeaderRightPart >div {
    background-color:  rgba(255,255,255, 0.06);
    border-radius: 5px;
    padding-left: 3px;
    padding-right: 3px;
    height: 24px;
}
.AppHeaderRightPart_1200Hours {
    display: flex;
    gap: 10px;
    align-items: center;
}
.AppHeaderRightPart_Value {
    color: #FFFFFF;
}